import { PoolApiInfo, PosPoolsRelated } from '@/types'
import { formatPercentage, isAvailableObject } from '@cetus/utils'
import { d, TickMath } from '@cetusprotocol/common-sdk'

export default function usePositionApr() {
  const estPositionAPRWithMultiMethod = (lowerUserPrice: number, upperUserPrice: number, lowerHistPrice: number, upperHistPrice: number) => {
    const retroLower = Math.max(lowerUserPrice, lowerHistPrice)
    const retroUpper = Math.min(upperUserPrice, upperHistPrice)
    const retroRange = retroUpper - retroLower
    const userRange = upperUserPrice - lowerUserPrice
    const histRange = upperHistPrice - lowerHistPrice

    const retroRangeD = d(retroRange.toString())
    const userRangeD = d(userRange.toString())
    const histRangeD = d(histRange.toString())

    if (retroRange <= 0) return d(0)
    if (retroRange === userRange) return histRangeD.div(retroRangeD)
    if (retroRange === histRange) return retroRangeD.div(userRangeD)
    return retroRangeD.mul(retroRangeD).div(histRangeD).div(userRangeD)
  }

  const getPositionApr = (posPoolInfo: PoolApiInfo, posRangeInfo: PosPoolsRelated, dateType: 'day' | 'week' | 'month', range: any) => {
    if (!isAvailableObject(posPoolInfo) || !isAvailableObject(posRangeInfo) || !isAvailableObject(range)) return
    const { minPrice, minPriceResever, maxPrice, maxPriceResever } = posRangeInfo
    const { tokenA, tokenB, miningAprList, feeApr, feeApr7d, feeApr30d, isReverse } = posPoolInfo
    console.log('🚀🚀🚀 ~ usePositionApr.ts:27 ~ getPositionApr ~ isReverse:', isReverse)
    const decimalA = tokenA?.decimals
    const decimalB = tokenB?.decimals
    const isFullRange = minPrice == '0' && maxPrice == '∞'
    const lowerUserPrice = isReverse ? minPriceResever : minPrice
    const upperUserPrice = isFullRange ? 2 ** 50 : isReverse ? maxPriceResever : maxPrice
    console.log('🚀🚀🚀 ~ usePositionApr.ts:31 ~ getPositionApr ~ lowerUserPrice:', lowerUserPrice)
    console.log('🚀🚀🚀 ~ usePositionApr.ts:33 ~ getPositionApr ~ upperUserPrice:', upperUserPrice)

    const { lower, upper } = range
    const beforeContractPriceLowest = TickMath.tickIndexToPrice(lower, decimalA, decimalB).toNumber()
    const beforeContractPriceHighest = TickMath.tickIndexToPrice(upper, decimalA, decimalB).toNumber()
    const positionMulti = estPositionAPRWithMultiMethod(lowerUserPrice, upperUserPrice, beforeContractPriceLowest, beforeContractPriceHighest)
    console.log('🚀🚀🚀 ~ usePositionApr.ts:41 ~ getPositionApr ~ positionMulti:', positionMulti.toString())
    const aprByFee = d(dateType == 'day' ? feeApr : dateType == 'month' ? feeApr30d : feeApr7d)
      .mul(positionMulti)
      .toString()
    const posMiningReward: any = []
    let aprPercentageTotal = d(aprByFee)
    miningAprList?.forEach(reward => {
      const posMiningRewardApr = d(positionMulti).mul(reward.apr).toString()
      posMiningReward.push({ ...reward, posMiningRewardApr })
      aprPercentageTotal = aprPercentageTotal.add(posMiningRewardApr)
    })

    console.log(`🚀🚀🚀 ~ usePositionApr.ts:45 ~ getPositionApr ~`, {
      aprPercentageTotal: aprPercentageTotal.toString(),
      aprByFee,
      posMiningReward
    })
    return {
      aprPercentageTotal: aprPercentageTotal.toString(),
      displayAprPercentageTotal: formatPercentage(aprPercentageTotal.toNumber() * 100),
      aprByFee,
      posMiningReward
    }
  }
  return { getPositionApr }
}
