import usePositionStore from '@/store/position'
import { PosBaseInfo } from '@/types'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { d } from '@cetus/utils'
import { Box, HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useEffect, useMemo, useRef, useState } from 'react'
import StatusPosition from './StatusPosition'

function PositionPriceRange({ positionInfo, width }: { positionInfo: PosBaseInfo; width: string }) {
  const { posPoolsRelatedData } = usePositionStore()
  const currentPosPoolsRelatedData = posPoolsRelatedData[positionInfo?.posId]
  const isActive = currentPosPoolsRelatedData?.currentStatus == 'Active'
  const { isApp } = useWindowWidth()

  const minPrice = useMemo(() => {
    return currentPosPoolsRelatedData?.minPriceOrigin
  }, [currentPosPoolsRelatedData])

  const maxPrice = useMemo(() => {
    return currentPosPoolsRelatedData?.maxPriceOrigin
  }, [currentPosPoolsRelatedData])

  const currPrice = useMemo(() => {
    return currentPosPoolsRelatedData?.currentPriceOrigin
  }, [currentPosPoolsRelatedData])

  const [elementWidth, setElementWidth] = useState<number>(0)
  const elementRef = useRef<HTMLDivElement>(null)

  useEffect(() => {
    const updateWidths = () => {
      if (elementRef.current) setElementWidth(elementRef.current.offsetWidth)
    }
    const resizeObserver = new ResizeObserver(updateWidths)
    if (elementRef.current) resizeObserver.observe(elementRef.current)
    return () => {
      if (elementRef.current) resizeObserver.unobserve(elementRef.current)
    }
  }, [minPrice, maxPrice, currPrice])

  const sliderML = useMemo(() => {
    if (maxPrice === '∞') return '0px'
    if (currPrice && minPrice && maxPrice && elementWidth) {
      if (d(currPrice).lessThanOrEqualTo(maxPrice) && d(currPrice).greaterThanOrEqualTo(minPrice)) {
        const relativePosition = d(currPrice)
          .sub(minPrice)
          .div(d(maxPrice).sub(d(minPrice)))
          .toString()
        return `${d(elementWidth).mul(relativePosition).toFixed(0)}px`
      }
      if (d(currPrice).lt(minPrice)) {
        return '6px'
      }
      if (d(currPrice).gt(maxPrice)) {
        return 'calc(100% - 6px)'
      }
    }
    return '0px'
  }, [currPrice, minPrice, maxPrice, elementWidth])

  return (
    <VStack align="flex-start" w={{ base: '100%', lg: width }} maxW={{ base: '100%', lg: width }}>
      <HStack maxW="100%" w={{ base: '100%', lg: 'unset' }} borderRadius="12px" pr="16px">
        <Skeleton isLoaded={!!currentPosPoolsRelatedData?.minPrice && !!currentPosPoolsRelatedData?.maxPrice} borderRadius="4px">
          <Text lineHeight="1" fontSize="14px" color="text_caption" whiteSpace="nowrap">
            {`${currentPosPoolsRelatedData?.minPrice} - ${currentPosPoolsRelatedData?.maxPrice}`}
          </Text>
        </Skeleton>
        <StatusPosition isActive={isActive} isLoading={currentPosPoolsRelatedData?.currentStatus == ''} />
      </HStack>
      <Skeleton h="6px" isLoaded={!!currentPosPoolsRelatedData?.currentPriceOrigin} borderRadius="4px">
        <Box w="88px" h="6px" bgImage={maxPrice === '∞' ? 'none' : "url('/images/img_inactive_bg@2x.png')"} bgSize="88px 6px">
          <Box
            w="88px"
            h={maxPrice === '∞' ? '2px' : '6px'}
            position="relative"
            ref={elementRef}
            background={
              maxPrice === '∞' ? 'linear-gradient(135deg, #68FFD8 0%, #0091FF 100%)' : isActive ? "url('/images/position_img_active@2x.png')" : 'none'
            }
            bgSize="100% 6px"
            bgPosition="center"
            bgRepeat="no-repeat"
          >
            {maxPrice !== '∞' && (
              <Box
                w="2px"
                h="8px"
                bg="#fff"
                borderRadius="8px"
                position="absolute"
                top="50%"
                left={sliderML}
                transform="translateY(-50%)"
                zIndex="5"
              />
            )}
          </Box>
        </Box>
      </Skeleton>
    </VStack>
  )
}

export default PositionPriceRange
