import useClaimPosition from '@/hooks/position/useClaimPosition'
import usePosHelper from '@/hooks/position/usePosHelper'
import usePositionApr from '@/hooks/position/usePositionApr'
import usePositionStore from '@/store/position'
import usePositionDetailStore from '@/store/position/detail'
import { PosBaseInfo } from '@/types'
import { AddressCopyLink, Block } from '@cetus/design'
import useExplorer from '@cetus/hooks/src/useExplorer'
import useWindowWidth from '@cetus/hooks/src/useWindowWidth'
import { cancelBubble, isAvailableObject } from '@cetus/utils'
import { Box, Button, HStack, Skeleton, Text, VStack } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import LiquidityValueBlock from './LiquidityValueBlock'
import PendingFeesBlock from './PendingFeesBlock'
import PendingRewardsBlock from './PendingRewardsBlock'
import PositionPriceRange from './PositionPriceRange'

function PositionItemContent({ positionInfo, positionItemWidth = [] }: { positionInfo: PosBaseInfo; positionItemWidth?: string[] }) {
  const { isApp } = useWindowWidth()
  const { getExplorerUrl } = useExplorer()
  const { getClmmPosName } = usePosHelper()
  const navigate = useNavigate()
  const { setCurrentPosDetailTab } = usePositionDetailStore()
  const { posPoolsOriginalData, posPoolsRelatedData, posApiPoolData, poolRangeObj } = usePositionStore()

  const currentPosPoolsRelatedData = posPoolsRelatedData[positionInfo?.posId]
  const posPoolInfo = posApiPoolData[positionInfo?.clmmPool]

  const tokenName = useMemo(() => {
    if (positionInfo?.tokenName) {
      return positionInfo?.tokenName
    } else {
      const position_index = posPoolsOriginalData?.[positionInfo?.clmmPool]?.index
      return getClmmPosName(Number(positionInfo?.index), position_index)
    }
  }, [positionInfo?.tokenName, positionInfo?.index, positionInfo?.clmmPool, posPoolsOriginalData])

  const clickDetail = () => {
    console.log('🚀 ~ PoolItem ~ poolInfo:', positionInfo)
    navigate(`/position-detail/${positionInfo?.id}`)
  }

  const [isShowAction, setIsShowAction] = useState(false)

  const { getPositionApr } = usePositionApr()
  const positionApr = useMemo(() => {
    if (isAvailableObject(currentPosPoolsRelatedData) && isAvailableObject(posPoolInfo) && isAvailableObject(poolRangeObj)) {
      console.log('🚀 ~ positionApr ~ poolRangeObj:', poolRangeObj)
      const currentRangeObj = poolRangeObj[positionInfo?.clmmPool]
      if (currentRangeObj) {
        const ranges = currentRangeObj?.ranges.reduce((acc: any, item: any) => {
          acc[item.dateType] = item
          return acc
        }, {})
        return getPositionApr(posPoolInfo, currentPosPoolsRelatedData, 'week', ranges['week'])
      }
    }
  }, [currentPosPoolsRelatedData, posPoolInfo, positionInfo, poolRangeObj])

  const { toClaimPosition, isClaimLoading } = useClaimPosition()
  const toClaim = () => {
    toClaimPosition(positionInfo, null, true)
  }

  const [fees, setFees] = useState(null)
  const [rewards, setRewards] = useState(null)
  return (
    <Block
      onMouseEnter={() => {
        setTimeout(() => {
          setIsShowAction(true)
        }, 40)
      }}
      onMouseLeave={() => {
        setTimeout(() => {
          setIsShowAction(false)
        }, 80)
      }}
      w="100%"
      bg="position_bg"
      border="none"
      p={{ base: '16px 8px 12px', lg: '20px 16px ' }}
      borderRadius="16px"
      onClick={clickDetail}
    >
      <HStack w="100%" justify="space-between" flexDirection={{ base: 'column', lg: 'row' }}>
        <VStack w="100%" align="flex-start">
          <HStack w="100%" justify="space-between" flexDirection={{ base: 'column', lg: 'row' }} gap={{ base: '0', lg: '8px' }}>
            {isApp && <Box h="4px" />}
            <PositionPriceRange positionInfo={positionInfo} width={positionItemWidth[0]} />
            {isApp && <Box h="4px" />}
            <HStack w={positionItemWidth[1]} justify="flex-end">
              <Skeleton isLoaded={!!positionApr}>
                <Text textAlign="right" color="text_caption">
                  {positionApr?.displayAprPercentageTotal}
                </Text>
              </Skeleton>
            </HStack>
            <VStack
              w={{ base: '100%', lg: positionItemWidth[2] }}
              justify={{ base: 'space-between', lg: 'unset' }}
              align="flex-end"
              flexDirection={{ base: 'row', lg: 'column' }}
            >
              <LiquidityValueBlock positionInfo={positionInfo} fontSize="14px" />
            </VStack>
            <PendingFeesBlock positionInfo={positionInfo} width={positionItemWidth[3]} onFeesChange={setFees} />
            <PendingRewardsBlock positionInfo={positionInfo} width={positionItemWidth[4]} onRewardsChange={setRewards} />
          </HStack>
        </VStack>
      </HStack>
      <Box
        sx={{
          maxHeight: isShowAction ? '44px' : '0',
          opacity: isShowAction ? 1 : 0,
          overflow: 'hidden',
          transition: 'all 0.2s ease'
        }}
      >
        <HStack w="100%" justify="space-between" mt="12px">
          <HStack flexDirection={{ base: 'column', lg: 'row' }} w={{ base: '100%', lg: 'unset' }}>
            <HStack w={{ base: '100%', lg: 'unset' }} justify={{ base: 'space-between', lg: 'flex-start' }}>
              <Text fontSize={{ base: '14px', lg: '12px' }}>NFT ID</Text>
              <Skeleton isLoaded={!!tokenName}>
                <Text fontWeight="500" fontSize={{ base: '14px', lg: '12px' }} color="text_caption">
                  {tokenName?.split('|')[1]}
                </Text>
              </Skeleton>
            </HStack>
            <HStack w={{ base: '100%', lg: 'unset' }} justify={{ base: 'space-between', lg: 'flex-start' }}>
              <Text fontSize={{ base: '14px', lg: '12px' }} ml={{ base: '0', lg: '8px' }}>
                Address
              </Text>
              <AddressCopyLink
                fontWeight="500"
                color="text_caption"
                address={positionInfo?.id}
                showLink={false}
                fontSize={{ base: '14px', lg: '12px' }}
                onClickLink={() => {
                  window.open(getExplorerUrl(positionInfo?.id, 'nftAddress'), '_blank')
                }}
              />
            </HStack>
          </HStack>
          <HStack>
            <Button
              variant="outline"
              fontSize="12px"
              bg="none"
              w="96px"
              h="32px"
              borderRadius="8px"
              onClick={() => setCurrentPosDetailTab('increase')}
            >
              Increase
            </Button>
            <Button variant="outline" fontSize="12px" bg="none" w="96px" h="32px" borderRadius="8px" onClick={() => setCurrentPosDetailTab('remove')}>
              Remove
            </Button>
            <Button
              variant="outline"
              fontSize="12px"
              bg="none"
              w="96px"
              h="32px"
              borderRadius="8px"
              onClick={e => {
                cancelBubble(e)
                toClaim()
              }}
              isLoading={isClaimLoading}
              disabled={isClaimLoading || (fees == '$0' && rewards == '$0')}
            >
              Claim Yield
            </Button>
            {/* <Button variant="outline" fontSize="12px" bg="none" w="96px" h="32px" borderRadius="8px">
              Move Range
            </Button> */}
          </HStack>
        </HStack>
      </Box>
    </Block>
  )
}

export default PositionItemContent
