import useVaultsListV2Store from '@/store/vaults-v2/useVaultsList'
import { Block } from '@cetus/design'
import { Token } from '@cetus/types'
import { CoinPairImage, VTextLabelBox } from '@cetus/ui-kit'
import { isAvailableObject } from '@cetus/utils'
import { Box, Flex, Text, VStack } from '@chakra-ui/react'
import { useMemo } from 'react'
import { VaultsAprTooltipContent } from '../list/common/VaultsAprBlock'
import VaultPositionRange from './VaultPositionRange'

type VaultInfoProps = {
  vaultId: string
  displayTokenA?: Token
  displayTokenB?: Token
  feeDisplay: string
  clmmPool: string
  isReverse: boolean
  minPrice?: string
  maxPrice?: string
  currPrice?: string
  category: string
}
function VaultInfo(props: VaultInfoProps) {
  const { vaultId, displayTokenA, displayTokenB, feeDisplay, minPrice, maxPrice, currPrice } = props
  console.log('🚀🚀🚀 ~ VaultInfo.tsx:25 ~ VaultInfo ~ props:', props)
  const { vaultListObj } = useVaultsListV2Store()

  const apiVaultInfo = useMemo(() => {
    if (isAvailableObject(vaultListObj) && vaultId) {
      return vaultListObj[vaultId]
    }
    return
  }, [vaultListObj, vaultId])

  return (
    <VStack width="calc(100% + 32px)" padding="0 16px" alignItems="left" paddingBottom="16px" borderBottom="1px solid" borderColor="border">
      <Flex>
        <CoinPairImage
          coinAIconUrl={displayTokenA?.logo_url}
          coinBIconUrl={displayTokenB?.logo_url}
          imageStyle={{
            w: '32px',
            h: '32px'
          }}
          imgBoxStyle={{
            w: '32px',
            h: '32px'
          }}
        />
        <Flex marginLeft="8px" justifyContent="space-between" alignItems="center" width="100%">
          <Box>
            <Flex alignItems="center">
              <Text fontSize="16px" color="text_caption">
                {`${displayTokenA?.symbol} - ${displayTokenB?.symbol}`}
              </Text>
              <Block w="unset" p="3px 12px 2px" borderRadius="12px" bg="none" borderColor="primary" marginLeft="8px">
                <Text fontSize="12px" color="primary">
                  {feeDisplay}
                </Text>
              </Block>
            </Flex>
          </Box>
          <Flex flexDirection="column">
            <VTextLabelBox
              wrapStyle={{
                gap: '2px',
                alignItems: 'flex-end !important'
              }}
              title="APY"
              titleStyle={{
                color: 'primary',
                fontSize: '12px'
              }}
              valueTooltip={
                <VaultsAprTooltipContent
                  vaultsLstApyDisplay={apiVaultInfo?.vaultsLstApyDisplay}
                  vaultsApyDisplay={apiVaultInfo?.vaultsApyDisplay}
                  vaultsTotalApyDisplay={apiVaultInfo?.vaultsTotalApyDisplay}
                  vaultsAprDisplay={apiVaultInfo?.vaultsAprDisplay}
                  category={apiVaultInfo?.category}
                />
              }
              value={apiVaultInfo?.category == 'cetus' ? apiVaultInfo?.vaultsTotalApyDisplay : apiVaultInfo?.vaultsApyDisplay || '-'}
              valueStyle={{
                color: 'text_highlight',
                h: '20px',
                lineHeight: '20px',
                fontSize: '14px',
                fontWeight: '500'
              }}
            />
          </Flex>
        </Flex>
      </Flex>
      <VaultPositionRange minPrice={minPrice} maxPrice={maxPrice} currPrice={currPrice} />
    </VStack>
  )
}

export default VaultInfo
